export default {
    // //准备state——用于存储数据
    state:{
        isCollapse:false,
        token:"",
    },
    //准备mutations——用于操作数据（state）
    mutations:{
        collapseMenu(state){
            state.isCollapse= !state.isCollapse  // 取反
        }

    }

}

